html {
  font-size: 16px;
}

:root {
  --golden-ratio: 1.61803;
}

*, :before, :after {
  box-sizing: border-box;
}

.app {
  min-height: 100vh;
  display: flex;
}

.app__container {
  padding: 1rem;
}

.container {
  max-width: 916px;
  width: 100%;
  margin: 0 auto;
}

.form__group--search {
  justify-content: center;
  column-gap: .5rem;
  display: flex;
}

.form > :not(script) {
  margin: 1rem 0;
}

.form > :not(script):first-child {
  margin-top: 0;
}

.form > :not(script):last-child {
  margin-bottom: 0;
}

.form__list {
  flex-wrap: wrap;
  padding: 0;
  list-style: none;
  display: flex;
}

.form__list:not(:empty) {
  margin: -.5rem;
}

.form__item {
  max-width: 100%;
  flex-grow: 1;
  padding: .5rem;
  display: flex;
}

@media (min-width: 480px) {
  .form__item {
    max-width: 50%;
  }
}

@media (min-width: 768px) {
  .form__item {
    max-width: 33.3333%;
  }
}

.item__label {
  flex-grow: 1;
  grid-template-rows: auto 1fr auto;
  grid-template-columns: auto 1fr;
  justify-content: flex-start;
  row-gap: .5rem;
  padding: 1rem;
  display: grid;
  position: relative;
}

.item__bg {
  z-index: -1;
  border: 1px solid #000;
  order: -3;
  position: absolute;
  inset: 0;
}

.item__title {
  white-space: nowrap;
  text-overflow: ellipsis;
  order: -2;
  grid-column: 1 / 3;
  font-size: 1.25rem;
  font-weight: 700;
  overflow: hidden;
}

.item__text {
  order: -1;
  grid-column: 1 / 3;
}

.item__checkbox {
  grid-column: 1 / 2;
}

.item__checkbox:checked ~ .item__bg {
  background-color: #695c5c;
  border-color: #695c5c;
}

.item__checkbox:checked ~ .item__title, .item__checkbox:checked ~ .item__text {
  color: #fff;
}

